.m-option-base {
  box-sizing: border-box;
  position: relative;
  margin-bottom: 0.5em;
}

input + script + .m-option-base {
  margin-top: 0.5em;
}

.m-option-base:first-child,
.m-option-base:nth-last-of-type(1) {
  margin-bottom: 0;
}

.m-option-base .a-icon-multistate {
  position: absolute;
  top: 0.1em;
  left: 0;
}

.balance .m-option-base {
  flex: 1 1 0;
  margin-right: 1em;
}

